<template>
	<view>
		<button type="default" @click="acquireInfo">微信登录</button>
	</view>
</template>

<script>
//这一版没加条件编译，全是应用在app上的
	import ifRigister from "../../common/ifRigister.js"
	export default {
		data() {
			return {
				options:{}
			}
		},
		//app 端微信登录
		onLoad: function(options) {
			this.options=options;
			//console.log(options);
		},

		methods: {
			acquireInfo:function(){
				let options=this.options; //任务结束后要跳转道德页面
				let that = this;
				//that.Login(options);
				uni.getProvider({
					service: 'oauth',
					success: function(res) {
						//console.log(res.provider);
						if (~res.provider.indexOf('weixin')) {
							uni.showModal({
								title: '微信授权登录',
								content: '是否同意获取您的微信信息？',
								success: function(res) {
									if (res.confirm) {
										that.Login(options);
									} else {
										uni.switchTab({
											url: '../write/write'
										})
									}
								}
							})
						}
					}
				})
			},
			 Login:function(options) {
				 uni.showLoading({
				 	title:'正在登录中'
				 })-
				uni.login({ //获取unionid和openid
					provider: 'weixin',
					success: (res) => {
						/*uni.showLoading({
							title:'登录中'
						})*/
						//console.log(res);
						uni.getUserInfo({ //获取用户名、头像、性别、年龄等等
							provider: 'weixin', //感觉不加这个也是微信端  这个是指的只在uniapp上调用微信第三方接口，而非发布在微信端
							success: (info) => {
								//console.log(info);
								ifRigister.ifrig(info.userInfo,options); //更改框架，直接在调用函数里搞动作，不用再返回值
							},
							fail: () => {
								uni.showToast({
									title: "微信登录授权失败"
								});
							}
						})
					},
					fail: () => {
						uni.showToast({
							title: "微信登录授权失败"
						});
					}
				})
			}

		}
	}
</script>

